@import "../../theme.scss";

.container {
  width: 100%;
  position: relative;
  overflow: hidden;

  .login-mod {
    height: 100%;
    padding-top: 70px;

    .login-container {
      position: relative;
      height: 100%;
      overflow: hidden;
      overflow-y: auto;

      .l-top {
        position: absolute;
        width: 184px;
        height: 235px;
        top: 0;
        left: 86px;
        background: url('../../images/top.png');
      }

      .l-middle {
        position: absolute;
        width: 554px;
        height: 367px;
        top: 149px;
        left: 18%;
        background: url('../../images/middle.png');
      }

      .l-bottom {
        position: absolute;
        width: 184px;
        height: 235px;
        bottom: 0;
        right: 86px;
        background: url('../../images/bottom.png');
      }
    }

    .login-box {
      position: relative;
      height: 800px;
      padding-top: 60px;
    }

    .login-inner {
      position: absolute;
      left: 56%;
      width: 390px;
      background: rgba(255, 255, 255, 1);
      box-shadow: 0px 4px 12px 0px rgba(0, 0, 0, 0.2);
      border-radius: 3px;
      padding: 32px;
      z-index: 1;

      .login-inner-logo {
        position: relative;
        height: 38px;

        div {
          position: absolute;
          top: 0;
          left: -10px;
          width: 154px;
          height: 38px;
          background: url("../../images/logo.png") no-repeat;
        }
      }

      .login-inner-head {
        margin: 18px 0 32px 0;

        h2 {
          margin: 0;
          height: 53px;
          font-size: 40px;
          color: rgba(0, 0, 0, 1);
          line-height: 53px;
        }
      }

      .login-content {
        p {
          margin: 0;
        }

        .login-item-name,
        .login-item-pwd {
          input {
            width: 100%;
            padding: 14px 16px;
            border-radius: 3px;
            border: 1px solid rgba(183, 183, 183, 1);
            font-size: 14px;
            font-weight: 400;
            color: rgba(128, 128, 128, 1);

            &:focus {
              border-color: $bg-color;
            }
          }
        }

        .login-forgetpwd {
          margin-bottom: 23px;
          text-align: right;

          span {
            height: 20px;
            font-size: 14px;

            font-weight: 400;
            color: rgba(128, 128, 128, 1);
            line-height: 20px;
            cursor: pointer;
          }
        }

        .login-item-loginbtn,
        .login-item-regbtn {
          button {
            width: 100%;
            height: 48px;
            background: $bg-color;
            border-radius: 3px;
            font-size: 14px;

            font-weight: 500;
            color: rgba(255, 255, 255, 1);
            line-height: 48px;
            border: 0;
            cursor: pointer;
          }
        }

        .login-item-regbtn {
          // margin-top: 16px;

          button {
            color: rgba(10, 10, 10, 1);
            background-color: #fff;
            border: 1px solid #b7b7b7;
          }
        }
      }
    }
  }

  //忘记密码
  .forgetpwd-model {
    position: fixed;
    bottom: 0;
    top: 0;
    left: 0;
    right: 0;
    background: rgba(0, 0, 0, 0.7);
    // box-shadow: 0px 4px 12px 0px rgba(0, 0, 0, 0.2);
    z-index: 99;

    p {
      margin: 0;
    }

    .forgetpwd-inner {
      position: relative;
      width: 100%;
      height: 100%;

      .forgetpwd-content {
        position: absolute;
        top: 150px;
        left: 50%;
        width: 668px;
        background: rgba(255, 255, 255, 1);
        box-shadow: 0px 4px 12px 0px rgba(0, 0, 0, 0.2);
        border-radius: 3px;
        margin-left: -334px;

        .forgetpwd-bd {
          position: relative;
          padding: 45px 175px;

          .close-icon {
            position: absolute;
            top: 10px;
            right: 15px;
            font-size: 20px;
          }

          .forgetpwd-head {
            margin-bottom: 30px;

            span {
              font-size: 40px;
              color: rgba(0, 0, 0, 1);
              line-height: 53px;
            }
          }

          .details {
            .forgetpwd_item {

              .forgetpwd_label {
                height: 48px;
                color: rgba(2, 2, 2, 1);
                line-height: 22px;
              }

              .forgetpwd_input {
                width: 100%;
                height: 48px;
                border-radius: 3px;
                border: 1px solid rgba(183, 183, 183, 1);
                color: rgba(128, 128, 128, 1);
                line-height: 48px;
                padding-left: 10px;
              }
            }

            .next {
              display: inline-block;
              width: 100%;
              height: 48px;
              background: $bg-color;
              border-radius: 3px;
              font-weight: 500;
              color: rgba(255, 255, 255, 1);
              line-height: 48px;
              text-align: center;
              cursor: pointer;
            }

            .black {
              display: inline-block;
              width: 100%;
              height: 48px;
              text-decoration: underline;
              line-height: 48px;
              cursor: pointer;
            }

            .user-name,
            .user-question {
              text-align: center;
              border-bottom: 1px solid #e9e9e9;

              .title {
                font-size: 18px;
                line-height: 30px;
              }

              .txt {
                line-height: 24px;
              }
            }
          }
        }
      }
    }

    @media screen and (max-width:668px) {
      .forgetpwd-inner {
        .forgetpwd-content {
          width: 100%;
          top: 45px;
          left: 0;
          margin: 0;

          .forgetpwd-bd {
            padding: 35px 100px;

            .forgetpwd-head {
              span {
                font-size: 34px;
                line-height: 34px;
              }
            }
          }
        }
      }
    }

    @media screen and (max-width:470px) {
      .forgetpwd-inner {
        .forgetpwd-content {

          .forgetpwd-bd {
            padding: 35px 15px;

            .forgetpwd-head {
              span {
                font-size: 34px;
                line-height: 34px;
              }
            }
          }
        }
      }
    }
  }

  @media screen and (max-width:992px) {
    .login-container {
      .login-inner {
        left: 50%;
        margin-left: -195px;
      }
    }
  }

  @media screen and (max-width:390px) {
    .login-container {
      .login-inner {
        width: 100%;
        top: 0;
        left: 0;
        right: 0;
        margin: 0;
        padding: 10px;

        .login-inner-head {
          margin: 0px 0 20px 0;
        }
      }
    }
  }

  #Footer {
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    z-index: 1;
  }
}